@import '../../../theme/src/base/_cx-bootstrap-variables.scss';

.input-group {
  &.input-group--prefix .input-group-addon.addon-prefix,
  &.input-group--suffix .input-group-addon.addon-suffix {
    background-color: $gray300;
    padding-right: $spacing-xs;
    padding-left: $spacing-xs;
  }

  & input.form-control + .addon-wrapper {
    border-left: 0px;
  }

  & .input-group-btn {
    & + .input-group-btn button.btn {
      margin-left: -1px;
    }

    & > button.btn.btn-default,
    & > .dropdown > button.btn.btn-default {
      border-radius: 0;
      border-color: $input-border;

      &.btn-primary {
        border-color: $btn-primary-border;
      }
    }

    &.input-group-dropdown-button {
      z-index: 3;
    }

    &.addon-icon > button {
      padding: 3px $spacing-s;
    }
  }

  &.disabled .input-group-btn > button,
  &.disabled .input-group-addon > .addon-svg-wrapper {
    opacity: 0.5;
    cursor: not-allowed;
  }

  & .input-group-addon.addon-icon {
    padding: 0px;
    & .addon-svg-wrapper {
      padding: 0 $spacing-xs;
    }
  }

  & .input-group-btn.addon-icon > button,
  & .input-group-addon.addon-icon > .addon-svg-wrapper {
    color: $grey530;
  }

  & .input-group-btn.addon-icon > button svg.svgicon,
  & .input-group-addon.addon-icon > .addon-svg-wrapper > svg.svgicon {
    display: block;
    margin: auto;
  }

  .input-group-btn:last-child > .btn,
  .input-group-btn:last-child > .btn-group & .input-group-btn,
  & .input-group-addon {
    &:last-child,
    &:last-child button.btn,
    &:last-child > .dropdown > button.btn {
      border-top-right-radius: $border-radius-base;
      border-bottom-right-radius: $border-radius-base;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
  }

  .input-group-btn:first-child > .btn,
  .input-group-btn:first-child > .btn-group & .input-group-btn,
  & .input-group-addon {
    &:first-child,
    &:first-child button.btn,
    &:first-child > .dropdown > button.btn {
      border-top-left-radius: $border-radius-base;
      border-bottom-left-radius: $border-radius-base;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }
  }
}

.has-error {
  & .input-group {
    & .input-group-addon.addon-icon {
      background-color: $brand-danger;
    }

    & .input-group-addon.addon-icon .addon-svg-wrapper,
    & .input-group-btn > button.btn,
    & .input-group-btn > .dropdown > button.btn,
    & .input-group-btn.addon-icon {
      border-color: $brand-danger;
      background-color: $brand-danger;
      color: $white;
    }

    & .input-group-btn {
      & > button.btn,
      & > .dropdown > button.btn {
        border-color: $brand-danger;

        & .addon-svg-wrapper,
        & svg.svgicon {
          color: $white;
        }
        & .caret {
          border-color: $white;
        }
      }
    }
  }
}
